import React, { useMemo, useState } from 'react';
import { HpGrid, HpGridItem, HpTabs, HpTabsBar, HpTabsPanel, HpTabsPanels, HpTabsTab } from '@htmlplus/react';
import * as constant from 'src/constant';
import { Example, FrameworkSelector, Markup, Parameters, Section, Text } from 'src/components';
import { components } from 'src/data';
import { LayoutDefault } from 'src/layouts';
import { Router } from 'src/services';
import { Utils } from 'src/utils';

const ComponentDetails: React.FC = () => {

    // TODO
    const [lang, setLang] = useState('react');

    const router = Router.useRouter();

    const [ready, setReady] = useState(false);

    const [selected, setSelected] = useState(undefined);

    const parent: any = useMemo(
        () => {

            const parent = components.find(({ key }) => key === router.match.params.key);

            if (!parent) return;

            return JSON.parse(JSON.stringify(parent))
        },
        [router.match.params.key]
    );

    const children: Array<any> = useMemo(
        () => {

            const result = [];

            result.push(parent);

            components
                .filter((component) => !component.parent && component.group === parent?.group)
                .map((component) => result.push(component));

            return result;
        },
        [parent]
    );

    const current: any = useMemo(
        () => {
            return components.find(({ key }) => key === (selected || parent?.key))
        },
        [parent, selected]
    );

    const tabs = useMemo(
        () => {

            const { properties, slots, events, styles, methods } = current;

            return [
                {
                    key: 'properties',
                    value: 'Properties',
                    items: properties.map((property: any) => ({
                        ...property,
                        default: property.default || 'undefined',
                        type: property.type.replace('"1" | "10" | "11" | "12" | "2" | "3" | "4" | "5" | "6" | "7" | "8" | "9"', '"1" ~ "12"') // TODO
                    }))
                },
                {
                    key: 'slots',
                    value: 'Slots',
                    items: slots
                },
                {
                    key: 'events',
                    value: 'Events',
                    items: events.map((event: any) => ({
                        ...event,
                        value: event.detail,
                        name: `on${constant.PLATFORM_EVENT_PREFIX}${Utils.capitalize(event.name)}`
                    }))
                },
                {
                    key: 'styles',
                    value: 'Styles',
                    items: styles
                },
                {
                    key: 'methods',
                    value: 'Methods',
                    items: methods.map((method: any) => ({ ...method, value: method.signature }))
                }
            ]
        },
        [current]
    );

    // TODO
    useMemo(
        () => {

            try {

                const { examples } = require(`@htmlplus/react/dist/docs/examples/${parent.key}`);

                parent.examples.forEach((example: any) => {

                    const code = (examples.find((item: any) => item.key === example.key) || {}).code;

                    example.script = code;
                });

                setReady(true);
            }
            catch { }

        },
        [parent.examples, parent.key]
    );

    const examples = useMemo(
        () => {

            if (!ready) return [];

            return parent.examples.map((example: any) => {

                example.preview = example.script.preview;

                example.code = {
                    ...example.script[lang],
                    style: example.script.style || ''
                }

                return example;
            });
        },
        [lang, parent.examples, ready]
    );

    const isDisabled = (key: string) => !current[key].length;

    const isActive = (key: string) => (tabs.find((tab) => !isDisabled(tab.key)) || {}).key === key;

    const hasApi = !!tabs.filter((tab) => isActive(tab.key)).length;

    const hasExamples = !!parent.examples.length;

    const hasReadme = !!parent.readme;

    const hasUsage = !!parent.usage

    if (!current) return null;

    return (
        <LayoutDefault>

            <FrameworkSelector onChange={(event: any) => setLang(event.target.value)} />

            <Section size="page">
                <Text>{parent.title}</Text>
                <Markup>{parent.description}</Markup>
            </Section>

            {hasReadme && (
                <Section size="page">
                    <Markup>{parent.readme}</Markup>
                </Section>
            )}

            {hasUsage && (
                <Section size="page">
                    <Text>Usage</Text>
                    <Text>Usage description</Text>
                    {/* <app-usage value={current} /> */}
                </Section>
            )}

            {hasApi && (
                <Section size="page">
                    <HpGrid align-items="center" justify="between">
                        <HpGridItem>
                            <Text>API</Text>
                        </HpGridItem>
                        <HpGridItem>
                            <select
                                value={selected}
                                disabled={children.length < 2}
                                onChange={(event: any) => setSelected(event.target.value)}
                            >
                                {children.map((item) => <option key={item.key} value={item.key}>{item.tag}</option>)}
                            </select>
                        </HpGridItem>
                    </HpGrid>
                    <Text>
                        Select your desired component from below and see the available properties, slots, events, styles and methods.
                    </Text>
                    <HpTabs value="properties">
                        <HpTabsBar grow>
                            {tabs.map((tab) => (
                                <HpTabsTab
                                    key={tab.key}
                                    disabled={isDisabled(tab.key)}
                                    value={tab.key}
                                >
                                    <Text>
                                        {tab.value}
                                    </Text>
                                </HpTabsTab>
                            ))}
                        </HpTabsBar>
                        <HpTabsPanels>
                            {tabs.map((tab) => (
                                <HpTabsPanel key={tab.key} value={tab.key}>
                                    <Parameters items={tab.items} />
                                </HpTabsPanel>
                            ))}
                        </HpTabsPanels>
                    </HpTabs>
                </Section>
            )}

            {hasExamples && (
                <Section size="page">
                    <Text>Examples</Text>
                    <Text>Below is a collection of simple to complex examples.</Text>
                </Section>
            )}

            {examples.map((example: any) => (
                <Section key={example.key} size="page">
                    <Text>{example.title}</Text>
                    <Markup>
                        {example.readme}
                    </Markup>
                    <Example value={example} />
                </Section>
            ))}
        </LayoutDefault>
    )
}

export default ComponentDetails;
